<template>
  <div class="clearfix my-page-list">
    <ul class="page-item-wrapper">
      <li class="page-item create" @click="createNewPage">
        <div class="temp-create">
          <i class="el-icon-plus"></i>
          <p class="paddingT10">创建H5</p>
        </div>
        <div class="null-create">
          空白创建
        </div>
      </li>
      <li class="page-item" v-for="(item, index) in pageList" :key="index">
        <div class="header-mask">
          <div class="details-btn"  @click="showPreviewFn(item._id)">预览</div>
        </div>
        <div class="cover" :style="{backgroundImage: `url(${item.coverImage || defaultCoverImage})`}"></div>
        <div class="page-item-title border-T">
          <span class="item-title-i">未命名页面</span>
        </div>
        <div class="page-item-data-pv border-T">
          <div class="btn-wrapper">
            <el-button type="text" size="mini" @click="editPage(item._id)">编辑</el-button>
          </div>
          <div class="btn-wrapper">
            <el-button type="text" size="mini" @click="copyPage(item._id)">复制</el-button>
          </div>
          <div class="btn-wrapper">
            <el-button type="text" size="mini" @click="deletePage(item._id, index)">删除</el-button>
          </div>
        </div>
      </li>
    </ul>
    <!--预览-->
    <previewPage v-if="showPreview" :pageId="previewId" @closePreview="showPreview = false"></previewPage>
  </div>
</template>

<script>
	import editorProjectConfig from '../editor/DataModel'
	import previewPage from '@client/components/preview-page'


	export default {
		components: {
			previewPage
		},
		data() {
			return {
				loading: false,
				defaultCoverImage: require('../../common/images/quark--pagecover-image.jpg'),
				pageList: [],
				previewId: '',
				showPreview: false
			}
		},
		created() {
			this.getPageList();
		},
		methods: {
			/**
			 * 获取所有页面
			 */
			getPageList() {
				this.$axios.get('/page/myPages').then(res => {
					this.pageList = res.body || []
				})
			},
			/**
			 * 创建页面,初始化数据提交接口,然后把id拿到后跳转到编辑器页面
			 */
			createNewPage() {
				let newPageData = editorProjectConfig.getProjectConfig()
				this.loading = true;
				this.$axios.post('/page/add', {...newPageData, author: this.$store.state.user.userId}).then(res => {
					this.loading = false;
					if (res.body) {
						this.$router.push({path: 'editor', query: {id: res.body._id}})
					}
				}).catch(() => {
					this.loading = false;
				})
			},
			/**
			 * 编辑页面
			 * @param id
			 */
			editPage(id) {
				this.$router.push({path: 'editor', query: {id: id}})
			},
      /**
       * 复制页面
       */
			copyPage(id){
				this.loading = true;
				this.$axios.post('/page/copy/' + id).then(res => {
					this.loading = false;
					if (res.body) {
						this.$router.push({path: 'editor', query: {id: res.body._id}})
					}
				}).catch(() => {
					this.loading = false;
				})
      },
			/**
			 * 删除页
			 * @param id
			 * @param index
			 */
			deletePage(id, index) {
				this.$confirm('此操作将永久删除该页面, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.loading = true;
					this.$axios.delete('/page/delete/' + id).then(() => {
						this.loading = false;
						// 从页面删除
						this.pageList.splice(index, 1)
					}).catch(() => {
						this.loading = false;
					})
				})
			},
			showPreviewFn(id) {
				this.previewId = id
				this.showPreview = true;
			}
		}
	}
</script>

<style lang="scss" scoped>
  .my-page-list{
  }
  .page-item {
    width: 224px;
    height: 296px;
    border-radius: 4px;
    overflow: hidden;
    float: left;
    margin-bottom: 24px;
    background: white;
    position: relative;
    transition: all 0.28s;
    &:hover{
      box-shadow: 0 0 16px 0 rgba(0,0,0,.16);
      transform: translate3d(0,-2px,0);
      .header-mask{
        opacity: 1;
      }
    }
    .header-mask{
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      background-color: rgba(0,0,0,.8);
      width: 224px;
      height: 224px;
      border-radius: 4px 4px 0 0;
      padding-top: 92px;
      text-align: center;
      transition: top .28s ease,opacity .28s ease,height .28s ease;
      .details-btn{
        display: inline-block;
        width: 120px;
        height: 44px;
        font-size: 18px;
        line-height: 44px;
        border-radius: 22px;
        border: 1px solid #fff;
        color: #fff;
        cursor: pointer;
      }
    }
    .cover{
      width: 224px;
      height: 224px;
      background-size: cover;
    }
    .page-item-title{
      .item-title-i{
        display: inline-block;
        width: 85%;
        font-size: 14px;
        height: 36px;
        line-height: 36px;
        color: #111;
        transition: transform .2s ease;
        background-color: #fff;
        padding: 0 8px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        float: left;
      }
    }
    .page-item-data-pv{
      width: 100%;
      display: flex;
      .btn-wrapper{
        flex: 1;
        text-align: center;
      }
    }
  }
  .page-item:not(:nth-child(4n+1)) {
    margin-left: calc((100% - 224px * 4) / 3);
  }

  .page-item.create{
    padding: 16px;
    text-align: center;
    .temp-create{
      display: inline-block;
      width: 192px;
      height: 204px;
      border: 1px solid #e6ebed;
      border-radius: 3px;
      margin-bottom: 18px;
      padding-top: 70px;
      transition: all 0.28s;
      cursor: pointer;
      &:hover{
        color: $primary;
        border-color: $primary;
      }
    }
    .null-create{
      display: inline-block;
      width: 192px;
      height: 42px;
      line-height: 42px;
      border: 1px solid #e6ebed;
      transition: all 0.28s;
      cursor: pointer;
      &:hover{
        color: $primary;
        border-color: $primary;
      }
    }
  }
</style>
